<template>
    <div>
        <u-button @click.native="start">Start</u-button>
        <u-button @click.native="finish">Finish</u-button>
        <u-button @click.native="error">Error</u-button>
    </div>
</template>
<script>
    export default {
        methods: {
            start () {
                this.$Loading.start();
            },
            finish () {
                this.$Loading.finish();
            },
            error () {
                this.$Loading.error();
            }
        },
        created () {
//            this.$Loading.config({
//                color: '#5cb85c',
//                failedColor: '#f0ad4e',
//                height: 5
//            });
        }
    };
</script>



